import { request } from 'utils';

const prefix = '/api/categories';

export const getListTown = (page = 1, IsPagingEnabled = true) =>
  request({
    prefix,
    url: '/Town',
    params: {
      page,
      IsPagingEnabled,
    },
    method: 'GET',
  });
export const getTown = id =>
  request({
    prefix,
    url: `/Town/GetById`,
    params: { id },
    method: 'GET',
  });
export const deleteTown = id =>
  request({
    prefix,
    url: `/Town`,
    data: { id },
    method: 'DELETE',
  });
export const updateTown = (id, values) =>
  request({
    prefix,
    url: `/Town`,
    data: {
      id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
    },
    method: 'PUT',
  });
export const createTown = values =>
  request({
    prefix,
    url: '/Town',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
    },
    method: 'POST',
  });



