import { request } from 'utils';

const prefix = '/api/categories';
export const getDistricts = ({
  label,
  labelInEnglish,
  townId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/District',
    method: 'GET',
    params: { label, labelInEnglish, townId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getDistrictById = id =>
  request({
    prefix,
    url: `/District/GetById`,
    method: 'GET',
    params: { id },
    data: {},
  });
export const createDistric = data =>
  request({
    prefix,
    url: '/District',
    method: 'POST',
    params: {},
    data,
  });
export const updateDistrict = (id, data) =>
  request({
    prefix,
    url: `/District`,
    method: 'PUT',
    params: {},
    data: { ...data, id },
  });
export const deleteDistrict = id =>
  request({
    prefix,
    url: `/District`,
    method: 'DELETE',
    params: {},
    data: { id },
  });



