import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Card, Form, Input, message, Select } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';

import { getDistrictById, updateDistrict, createDistric } from 'services/district';
import { getListTown } from 'services/town';

const { Option } = Select;
const TIME_RETURN = 1500;

@Form.create({ name: 'formSpcializeTraining' })
class FormDistrict extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      initialValue: {},
      listTown: [],
    };
    this.updateTitle = 'Cập nhật huyện';
    this.addNewTitle = 'Thêm huyện';
  }

  componentDidMount = async () => {
    const { match } = this.props;
    const listTown = await getListTown();
    if (match.params.id) {
      const DistrictByID = await getDistrictById(match.params.id);
      const initialValue = this.handleInitialValue(DistrictByID.data);
      this.setState({
        id: match.params.id,
        initialValue,
        listTown: listTown.data.items,
      });
    } else {
      this.setState({
        listTown: listTown.data.items,
      });
    }
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      id: '',
      townId: '',
      label: '',
      labelInEngLish: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        // edit
        if (this.state.id) {
          values.id = this.state.id;
          updateDistrict(values.id, values).then(res => {
            console.log('res update', res);
            if (res.status === 200) {
              message.success('Cập nhật thành công.');
              setTimeout(() => this.props.history.goBack(), TIME_RETURN);
            } else {
              message.error(res.data.message);
            }
          });
        } else {
          createDistric(values).then(res => {
            console.log('res create', res);
            if (res.status === 200) {
              message.success('Tạo huyện thành công.');
              setTimeout(() => this.props.history.goBack(), TIME_RETURN);
            } else {
              message.error(res.data.message);
            }
          });
        }
      } else {
        console.log('err form', err);
      }
    });
  };

  render() {
    const { initialValue, listTown } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { id: districtId } = this.state;
    const disableId = !!districtId;
    return (
      <Card title={districtId ? this.updateTitle : this.addNewTitle}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Tên tỉnh" hasFeedback>
              {getFieldDecorator('townId', {
                initialValue: initialValue.townId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select value={initialValue.townId}>
                  {listTown.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ký hiệu huyện" hasFeedback>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={disableId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên huyện huyện" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEngLish', {
                initialValue: initialValue.labelInEnglish,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDistrict {...props} />
  </Page>
));



